import http from "./base";

const passwordResetApi = {
  async getPasswordResetInfo(uuid: string): Promise<void> {
    await http.get(`/api/password-reset?token=${uuid}/`);
  },
  async resetPassword(
    email: string,
    password: string,
    token: string
  ): Promise<void> {
    const payload = { email, password, token };
    await http.post("/api/password-reset", payload);
  },
};

export default passwordResetApi;
