import {
  createRouter,
  createWebHistory,
  RouteLocationNormalized,
  RouteRecordRaw,
} from "vue-router";
import ResetPassword from "../components/ResetPassword.vue";
import PasswordResetSuccessful from "../components/PasswordResetSuccessful.vue";
import InvalidLink from "../components/InvalidLink.vue";

export const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "resetPassword",
    component: ResetPassword,
    props: (route) => ({
      email: route.query.email as string,
      token: route.query.token as string,
    }),
  },
  {
    path: "/success",
    name: "passwordResetSuccessful",
    component: PasswordResetSuccessful,
  },
  {
    path: "/invalid-link",
    name: "invalidLink",
    component: InvalidLink,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export async function routerBeforeEachGuard(
  to: RouteLocationNormalized,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  _from: RouteLocationNormalized
) {
  if (to.name === "resetPassword") {
    const expires_at_timestamp_str = to.query.expires_at
      ? (to.query.expires_at as string)
      : "";
    const expires_at_timestamp = parseInt(expires_at_timestamp_str, 10);
    const linkExpired = new Date().getTime() / 1000 > expires_at_timestamp;

    const matchesEmailPattern = String(to.query.email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );

    if (linkExpired || !matchesEmailPattern || !to.query.token) {
      return { name: "invalidLink" };
    }
  }
}

router.beforeEach(routerBeforeEachGuard);

export default router;
